/*menu */
.main-navbar .navbar-brand {
  background: url("../images/stema.svg") no-repeat left;
  background-size: 55px;
  display: table;
  font-size: 14px;
  font-weight: bold;
  height: 90px;
  line-height: 1.286em;
  margin-right: 0;
  padding: 0 0 0 70px; }

.main-navbar.demo-navbar .navbar-brand {
  background-image: url("http://placehold.it/110x110");
  background-position: left;
  background-repeat: no-repeat; }

.main-navbar .navbar-brand span {
  display: table-cell;
  vertical-align: middle; }

.main-navbar .navbar-brand:hover {
  color: #333; }

.main-navbar {
  clear: both;
  height: 110px;
  padding-top: 1rem; }

.menu-navbar ul {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0;
  margin-left: -1.5rem;
  margin-right: -1.5rem;

  li {
    color: #333;
    font-size: 14px;
    font-weight: 700;
    letter-spacing: 1.04px;
    text-transform: uppercase;
    position: relative;

    &:hover > {
      ul {
        display: block; } }

    &.active {
      background-color: #287dc3;

      a {
        color: #fff; }

      ul {
        li {
          a {
            color: #333; } } } }

    a {
      padding: 1.5rem;
      display: inline-block;
      color: #333; }

    ul {
      background-color: #fff;
      position: absolute;
      top: 100%;
      display: block;
      box-shadow: 0 20px 10px rgba(0,0,0, .16);
      margin: 0;
      border-radius: 0 0 3px 3px;
      width: 257px;
      display: none;
      padding-top: 7px;
      padding-bottom: 7px;

      li {
        a {
          font-size: 1.5rem;
          font-weight: normal;
          text-transform: none;
          display: block;
          padding: 1rem 2rem;
          position: relative;
          z-index: 2;
          &:hover {
            text-decoration: none; } }
        &:hover {
          background-color: rgb(247,247,249);
          color: #333; }
        &:last-child a {
          border-radius: 0 0 3px 3px; }

        &.has-submenu:before {
          background-image: url("../images/icons/arrow-gray-r.svg");
          background-repeat: no-repeat;
          background-size: contain;
          content: '';
          position: absolute;
          right: 19px;
          top: 14px;
          width: 6.5px;
          height: 13px;
          z-index: 1; }

        &.active {
          background-color: rgb(247,247,249);
          a {
            color: #333; } } }


      ul {
        left: 100%;
        top: -7px;
        border-radius: 3px;

        li:first-child a {
          border-radius: 3px 3px 0 0; } } } } }

.navbar-nav {
  margin-right: 0;
  padding: 25px 0;
  display: flex;
  align-items: center; }

/*top dropdown menu */
.dropdown.open {
  position: relative; }

.dropdown.open:after {
  border-color: #eee;
