@media (max-width: 767px) {
  #page-content {
    padding-top: 110px; }

  #main-menu {
    position: absolute;
    top: 100px;
    border: none;
    left: 0;
    width: 100%;
    background-color: #fff;

    .navbar-nav {
      -ms-flex-pack: center;
      justify-content: center; } }

  .menu-navbar {
    position: absolute;
    top: 163px;
    width: 100%;
    background-color: #fff;
    text-align: center;
    & > {
      ul {
        -ms-flex-direction: column;
        flex-direction: column; } }
    ul {
      li {
        ul {
          padding-top: 15px;
          padding-bottom: 15px;
          position: relative;
          top: 0;
          left: 0;
          box-shadow: none;
          background-color: rgb(239,240,240);
          border-radius: 0;
          width: 100%;
          &:before {
            background-color: rgba(0,0,0,0.06);
            background: linear-gradient(to bottom, rgba(0,0,0,0.06) 0%, rgba(255,255,255,0) 100%);
            content: '';
            height: 10px;
            width: 100%;
            position: absolute;
            top: 0;
            left: 0; }
          li.has-submenu:before {
            transform: rotate(90deg); }
          ul {
            background-color: rgb(250,250,250);
            top: 0;
            left: 0; } } } } } }
