.content {
  &.tabular {
    table {
      width: 100%;
      font-size: $fz1-6;
      thead {
        border-bottom: 1px solid $blue;
        font-size: $fz1-4;
        td,th {
          font-weight: bold; } }
      th,td {
        padding: 10px;
        &:first-child {
          padding-left: 0; }
        &:last-child {
          padding-right: 0; }
        em,i {
          color: $dark-medium; } }
      th {
        color: $blue;
        line-height: 2.7rem;
        font-weight: normal; } } }
  &.tabular-va-head-bottom {
    table {
      thead {
        th,td {
          vertical-align: bottom;
          line-height: 1.9rem !important;
          min-width: 100px; } } } }
  &.tabular-head-fz-1-4 {
    table {
      thead {
        th,td {}
        font-size: $fz1-4;
        color: $blue;
        text-transform: uppercase; } } }
  &.tabular-va-body-top {
    tbody {
      th,td {
       vertical-align: top; } } } }

.panel-group {
  & .panel-default {
      float: none;
      background: transparent;
      border: 1px solid $dark-light;
      border-radius: 4px;
      box-shadow: none;
      .panel-heading {
        background: transparent;
        border: none;
        padding: 0;
        .panel-title {
          a {
            position: relative;
            padding: 15px 30px;
            &:before {
              background: url('../images/icons/arrow-down.svg');
              background-repeat: no-repeat;
              content: '';
              height: 15px;
              width: 15px;
              position: absolute;
              right: 15px;
              top: 15px;
              transition: .3s all;
              @include rotate(180deg); }
            &.collapsed:before {
              top: 20px;
              @include rotate(0deg); } } } }

      .panel-body {
        border: none !important; } } }

.pos-relative {
  position: relative; }

.box-shadow {
  box-shadow: 0 10px 13px rgba(0,0,0,.13); }

@media (min-width: $layout-breakpoint-small) {
  .hidden-md {
    display: none; } }

@media (max-width: $layout-breakpoint-small) {
  .hidden-sm {
    display: none; } }

@media (max-width: $layout-breakpoint-xsmall) {

  .show-md {
    display: block !important; }

  .d-ms-flex {
    display: flex; }

  .ov-sm-scroll-x {
    overflow-x: scroll; }

  .hidden-xs {
    display: none; }

  .text-sm-center {
    text-align: center;
    &>img {
      margin-left: auto;
      margin-right: auto; } }

  .content img[style="float: right"] {
    float: none !important; }

  .flex-sm-column {
    -webkit-box-orient: vertical !important;
    -webkit-box-direction: normal !important;
    -ms-flex-direction: column !important;
    flex-direction: column !important; }

  .no-bd-sm {
    border: none !important; }

  .mb-sm-5 {
    margin-bottom: 1.5rem; }

  .content {
    &.tabular {
      .table-wrap {
        overflow-y: hidden;
        td,th {
          min-width: 120px; } } } }

  .justify-sm-content-center {
    -webkit-box-pack: center !important;
    -ms-flex-pack: center !important;
    justify-content: center !important; } }
